/*---------------------------------

  App

---------------------------------*/

#main {
  margin-top: 50px;
  text-align: center;
}

/*---------------------------------

  Modal

---------------------------------*/

.closebutton {
  font-size: 20px;
}

#overlay {
  /*　画面全体を覆う設定　*/
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);

  /*　画面の中央に要素を表示させる設定　*/
  display: flex;
  align-items: center;
  justify-content: center;
}

#inneroverlay {
  z-index: 2;
  /* width%で指定 */
  width: 50%;
  height: 120px;
  padding: 10px;
  background: #fff;

  display: flex;
  align-items: center;
  justify-content: center;
}

#content {
  text-align: center;
}
